.form {
  display: flex;
  border-top: 2px solid #D3D3D3;
}

.input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 80%;
  font-size: 1.2em;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  background: rgba(194, 194, 163,0.5);
  /*padding: 20px;*/
  display: inline-block;
  border: none;
  width: 20%;
}